<template>
  <div class="echart">
    <p >{{ msg }}</p>
    <div id="myChart" :style="{width: '800px', height: '600px'}"></div>
    <button @click="addNum"></button>
  </div>
</template>

<script>
  export default {
    name: "echart",
    data() {
      return {

      }
    },
    beforeCreate:function () {
      console.log("")
    },
    mounted() {
      this.drawLine();
    },
    computed:{
      msg(){
        return this.$store.state.adminInfo.count
      }
    },
    methods: {
      addNum(){
        console.log(this.$store.state.adminInfo.count++)
      },
      drawLine() {
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
          title: {text: 'Vueecharts'},
          tooltip: {},
          xAxis: {
            data: ["", "", "", "", "", ""]
          },
          yAxis: {},
          series: [{
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }]
        });
      }
    }
  }
</script>

<style scoped>

</style>



